const TogglePortfolio = ({Topic}) => {

    const NavigateTo = (path) => {
        if(path == Topic) return;
        else if(path == "games")
            navigate('?topic=games');
        else
            navigate('?topic=web');
    }

    return(<div className="toggle-portfolio">
        <input type={"button"} value="Web" onClick={()=>NavigateTo('web')} />
        <input type={"button"} value="Games" onClick={()=>NavigateTo('games')}/>
    </div>)
}

const Portfolio = ({location}) => {

    const params = new URLSearchParams(location.search);
    const topic = params.get("topic");
    console.log("para");

return(
<Layout location={location} title={"Portfolio"}>

    <TogglePortfolio Topic={topic} />

    <Seo title="Portfolio" />
    <h2>Portfolio</h2>

    <p>These are some of the projects I was involved in.</p>

    {
    (topic==null || topic=="games")   ?
    (<><PortfolioSection data={data} />
    <p>&nbsp;</p>
    <p>Apparently, that was some projects I did. I need to learn more by doing several game projects with great game studios out there.</p>
    <p>&nbsp;</p></>)
    :
    (<><PortfolioSection data={dataweb} /></>)
    }
    

</Layout>
)}

export default Portfolio